import { styled } from "@rp/react/theme"
import { Box, SxProps, Typography } from "@rp/react/web/mui/core"

const ShowMoreButtonStyled = styled(Box, {
  name: "ShowMoreButton",
  shouldForwardProp: prop => prop !== "isFamilyButton",
})<{
  isFamilyButton: boolean
}>(({ theme, isFamilyButton }) => {
  return isFamilyButton
    ? {
        display: "flex",
        margin: "0 auto",
        width: "fit-content",
        padding: "4px 0",
        textDecoration: "underline",
        "&:hover": {
          textDecoration: "none",
        },
      }
    : {
        display: "flex",
        alignItems: "center",
        color: theme.palette.text.secondary,
        marginTop: theme.spacing(0.5),
        marginLeft: 30,
        textTransform: "none",
        padding: "4px 0",
        width: "fit-content",
        textDecoration: "underline",
        "&:hover": {
          textDecoration: "none",
        },
      }
})

type Props = {
  showMore: boolean
  onClick: () => void
  qty: number
  isFamilyButton?: boolean
  sx?: SxProps
}

const ShowMoreButton = ({ showMore, onClick, qty, isFamilyButton = false, sx }: Props) => (
  <ShowMoreButtonStyled
    isFamilyButton={isFamilyButton}
    onClick={onClick}
    data-testid="show-more-button"
    sx={sx}
  >
    {showMore ? (
      <Typography sx={{ cursor: "pointer" }} variant="body2">
        Show less
      </Typography>
    ) : (
      <Typography sx={{ cursor: "pointer" }} variant="body2">
        Show more ({qty})
      </Typography>
    )}
  </ShowMoreButtonStyled>
)

export { ShowMoreButton }
